import React, { useState } from 'react';
import { Clock } from "./Clock";
import { Result } from "./Result";
import Projects from "./Projects";
import { Button, 
         Field, 
         Header, 
         Info, 
         LabelText, 
         Asterisk, 
         ParagraphWarning, 
         Loading, 
         Failure } from "./styled";

import { useRatesData } from "./useRatesData";

export const Form = () => {
  const [result, setResult] = useState();
  const ratesData = useRatesData();

  const calculateResult = (currency, amount) => {
    const rate = ratesData.rates[currency];

    setResult({
      sourceAmount: +amount,
      targetAmount: amount * rate,
      currency,
    });
  }

  const [currency, setCurrency] = useState("EUR");
  const [amount, setAmount] = useState("");

  const onSubmit = (event) => {
    event.preventDefault();
    calculateResult(currency, amount);
  }

  return (
    <form onSubmit={onSubmit}>
      <Clock />
      <Projects />
      <Header>
        Currency Converter
      </Header>
      {ratesData.state === "loading"
        ? (
          <Loading>
            Second . . . <br />Loading exchange rates from the European Central Bank . . . üè¶
          </Loading>
        )
        : (
          ratesData.state === "error" ? (
            // eslint-disable-next-line jsx-a11y/accessible-emoji
            <Failure>
              Hmm... Something went wrong üòÆ Check that you are connected to the internet.<br />
            </Failure>
          ) : (
            <>
              <p>
                <ParagraphWarning>Converting from PLN (PL)<Asterisk> *</Asterisk></ParagraphWarning>
                <label>
                  <LabelText lightslategrey>Amount in 
                  <Asterisk> *</Asterisk> :</LabelText>
                  <Field 
                    value={amount}
                    onChange={({ target }) => setAmount(target.value)}
                    type="number" 
                    placeholder="Enter the amount in PLN" 
                    step="0.01" 
                    required />
                </label>
              </p>
              <p>
                <label>
                  <LabelText crimson>Currency:</LabelText>
                  <Field 
                    as="select"
                    value={currency}
                    onChange={({ target }) => setCurrency(target.value)}
                  >
                    {!!ratesData.rates && Object.keys(ratesData.rates).map(((currency) => (
                      <option
                        key={currency}
                        value={currency}
                      >
                        {currency}
                      </option>
                    )))}
                  </Field>
                </label>
              </p>
              <p>
                <Button>Convert!</Button>
              </p>

              <Info>
                Exchange rates are taken from the European Central Bank. <br />
              </Info>
              
              <Result result={result} />
            </>
          )
        )}
    </form>
  );
};
 
